@import url('https://fonts.googleapis.com/css?family=Nunito:300,400,600,700,800&display=swap&subset=cyrillic');
*,
*:before,
*:after {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}
button:focus {
  outline: var(--color-red) solid 1px;
}
a {
  text-decoration: none;
}
b {
  font-weight: 600;
}
button {
  border: none;
}
ul {
  list-style: none;
}
time {
  display: inline-block;
}
address {
  font-style: normal;
}
.h1 {
  color: var(--color-white);
  font-size: 42px;
  letter-spacing: 1.05px;
  font-weight: bold;
  padding-left: 30px;
}
@media screen and (max-width: 620px) {
  .h1 {
    font-size: 34px;
    padding-left: 0;
  }
}
.h2 {
  color: var(--color-white);
  font-size: 32px;
  letter-spacing: 0.8px;
